@import url(https://fonts.googleapis.com/css?family=Ubuntu);
body {
  font-family: 'Ubuntu', sans-serif;
}
h1 {
  text-align: center;
}
#editor-toolbar {
  background-color: #26282c;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: auto;
  border: 1px solid black;
}
.fab {
  color: #FFF;
  padding: 0 5px;
}

#editor {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin: 0 auto;
  resize: vertical;
  outline: none;
  min-height: 250px;
  padding: 10px;
  border-radius: 1px;
  border: 1px solid black;
  background-color: black;
  color: white;
}
.editor-toolbar-min {
  width: 50vw;
  
}
.editor-toolbar-max {
  width: 75vw;
}
#preview {
  min-height:450px;
  margin: 0 auto;
  resize: vertical;
  outline: none;
  padding: 10px;
  border-radius: 1px;
  border: 1px solid black;
  background-color: black;
  color: white;
}
#preview-toolbar {
  background-color: #26282c;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: auto;
  border: 1px solid black;
}
.preview-toolbar-min {
  width: 80vw;
}
.preview-toolbar-max {
  width: 100vw;
}
.spacer {
  min-height: 20px;
}
code.language-javascript {
  color: yellow;
}
img {
  height: 100px;
  width: 100px; 
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

